/* LoginSignup.css */

.loginsignup {
    width: 100%;
    height: 100vh;
    background: linear-gradient(180deg, #ffffff 0%, #b270a722 60%);
    display: flex;
    justify-content: center;
    align-items: center;
    perspective: 1000px;
  }
  
  .loginsignup-container {
    width: 350px;
    background: linear-gradient(180deg, #ffffff 0%, #bf029f22 60%);
    border-radius: 10px;
    padding: 40px 60px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    
   
  }
  
  
  
  .loginsignup-container h1 {
    font-size: 2rem;
    margin-bottom: 20px;
    text-align: center;
    color: #333;
 
  }
  
  .loginsignup-fields {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-bottom: 30px;
  }
  
  .loginsignup-fields input {
    height: 50px;
    width: 100%;
    padding: 0 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    font-size: 1rem;
    transition: all 0.3s ease;
  }
  
  .loginsignup-fields input:focus {
    border-color: #4285f4;
    box-shadow: 0 0 0 2px rgba(66, 133, 244, 0.2);
 
  }
  
  .loginsignup-container button {
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg, var(--primary-color), #af60b8);
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .loginsignup-container button:hover {
    background: linear-gradient(45deg, var(--primary-color), #af60b8);
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(66, 133, 244, 0.4);
  }
  
  .loginsignup-login {
    margin-top: 20px;
    text-align: center;
    font-size: 0.9rem;
    color: #666;
  }
  
  .loginsignup-login span {
    color: #4285f4;
    font-weight: bold;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .loginsignup-login span:hover {
    color: #3367d6;
    text-decoration: underline;
  }
  
  .loginsignup-agree {
    display: flex;
    align-items: center;
    margin-top: 15px;
    font-size: 0.8rem;
    color: #666;
  }
  
  .loginsignup-agree input[type="checkbox"] {
    margin-right: 10px;
  }

 
 
  
  