@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

.Hero {
  display: flex;
  align-items: center;
  height: calc(100vh - 70px);
  background: linear-gradient(135deg, #c68ac3, #ffffff);
  padding: 0 10%;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  animation: fadeInLeft 1s ease-out;
}

.hero-left h2 {
  color: #333;
  font-size: 26px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.hand-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}

.hand-hand-icon p {
  color: #333;
  font-size: 70px;
  font-weight: 700;
  margin: 0;
}

.hand-hand-icon img {
  width: 80px;
  animation: wave 5s infinite;
}

.hero-left > div > p {
  color: #333;
  font-size: 70px;
  font-weight: 700;
  margin: 0;
}

.hero-left > div > p:last-child {
  color: #ff4141;
  font-size: 70px;
  font-weight: 700;
}

.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 310px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background: linear-gradient(45deg, #ff4141, #ff7676);
  color: white;
  font-size: 22px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.hero-latest-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(255, 65, 65, 0.3);
}

.hero-latest-btn img {
  width: 20px;
}

.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeInRight 1s ease-out;
}

.hero-right img {
  width: 90%;
  max-width: 500px;
  animation: float 6s ease-in-out infinite;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes wave {
  0%, 100% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(20deg);
  }
}

@media (max-width: 1024px) {
  .Hero {
    flex-direction: column;
    text-align: center;
    height: auto;
    padding: 50px 5%;
  }

  .hero-left, .hero-right {
    width: 100%;
  }

  .hand-hand-icon {
    justify-content: center;
  }

  .hero-left > div > p, .hero-left > div > p:last-child {
    font-size: 50px;
  }

  .hero-latest-btn {
    margin: 30px auto;
  }
}

@media (max-width: 768px) {
  .hero-left > div > p, .hero-left > div > p:last-child {
    font-size: 40px;
  }

  .hand-hand-icon img {
    width: 60px;
  }

  .hero-latest-btn {
    width: 250px;
    height: 60px;
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .hero-left > div > p, .hero-left > div > p:last-child {
    font-size: 30px;
  }

  .hand-hand-icon img {
    width: 40px;
  }

  .hero-latest-btn {
    width: 200px;
    height: 50px;
    font-size: 16px;
  }
}