/* Container for the Offers section */
.Offers {
    display: flex;
    align-items: center;

    padding: 20px 200px;
    background: linear-gradient(135deg, #dbbdd9, #ffffff);
    
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: auto;
   
    overflow: hidden;
    width: 70%;
    
  }
  
  /* Left section styling */
  .offers-left {
    flex: 1;
    padding: 20px;
    text-align: left;
  }
  
  /* Heading styling */
  .offers-left h1 {
    font-size: 2.5em;
    color: #333;
    margin: 0;
    line-height: 1.2;
    transform: translateX(-20px);
    opacity: 0;
    animation: fadeInUp 1s forwards;
  }
  
  .offers-left h1:nth-child(2) {
    margin-top: 10px;
  }
  
  /* Paragraph styling */
  .offers-left p {
    font-size: 1.2em;
    color: #777;
    margin: 10px 0;
    transform: translateX(-20px);
    opacity: 0;
    animation: fadeInUp 1s 0.5s forwards;
  }
  
  /* Button styling */
  .offers-left button {
    padding: 10px 20px;
    font-size: 1em;
    color: #fff;
    background-color: #e74c3c;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
    transform: translateX(-20px);
    opacity: 0;
    animation: fadeInUp 1s 1s forwards;
  }
  
  .offers-left button:hover {
    background-color: #c0392b;
    transform: translateY(-3px);
  }
  
  /* Right section styling */
  .offers-right {
    flex: 1;
    text-align: center;
  }
  
  /* Image styling */
  .offers-right img {
    width: 50%;
    height: auto;
    border-radius: 15px;
    transform: rotateY(0deg);
    transition: transform 0.5s ease;
  }
  
  /* Image hover effect */
  .offers-right img:hover {
    transform: rotateY(10deg);
  }
  
  /* Animation for elements */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  