/* Popular.css */

.popular {
    padding: 50px 0;
    text-align: center;
  }
  
  .popular h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    transform: perspective(1000px) rotateX(0deg);
    transition: transform 0.5s ease;
  }
  
  .popular h1:hover {
    transform: perspective(1000px) rotateX(10deg);
  }
  
  .popular hr {
    width: 200px;
    margin: 20px auto;
    border: none;
    height: 3px;
    background: linear-gradient(90deg, transparent, #333, transparent);
  }
  
  .popular-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    perspective: 1000px;
  }
  
  .popular-item > div {
    transition: all 0.3s ease;
    transform: rotateY(0deg);
  }
  
  .popular-item > div:hover {
    transform: rotateY(10deg) scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .popular-item > div {
    animation: fadeIn 0.5s ease forwards;
    opacity: 0;
  }
  
  .popular-item > div:nth-child(1) { animation-delay: 0.1s; }
  .popular-item > div:nth-child(2) { animation-delay: 0.2s; }
  .popular-item > div:nth-child(3) { animation-delay: 0.3s; }
  .popular-item > div:nth-child(4) { animation-delay: 0.4s; }
  
  /* Add more nth-child rules if you have more items */
  
  @media (prefers-reduced-motion: reduce) {
    .popular h1,
    .popular-item > div {
      transition: none;
      animation: none;
    }
  }