/* Navbar.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

:root {
  --primary-color: #ff4141;
  --secondary-color: #626262;
  --background-color: #f3f4f6;
  --text-color: #171717;
  --transition-speed: 0.3s;
}

.Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 15px; /* Reduced padding */
    background: linear-gradient(135deg, #f4dcee, #fefefe);
    position: sticky;
    top: 0;
    z-index: 99;
    font-family: 'Poppins', sans-serif;
    font-size: 14px; /* Reduced font size */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: all var(--transition-speed) ease;
    height: 80px; /* Set explicit smaller height */
  }
  
  
.Navbar:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.Nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all var(--transition-speed) ease;
}

.Nav-logo img {
  width: 40px;
  height: 40px;
  transition: all 0.5s ease;
}

.Nav-logo:hover img {
  transform: rotate(360deg) scale(1.2);
  filter: drop-shadow(0 0 10px var(--primary-color));
}

.Nav-logo p {
  color: var(--text-color);
  font-size: 38px;
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  transition: all var(--transition-speed) ease;
}

.Nav-logo:hover p {
  color: var(--primary-color);
  transform: scale(1.05);
}

.nav_menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 50px;
  perspective: 1000px;
}

.nav_menu li {
  color: var(--secondary-color);
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-speed) ease;
  position: relative;
  transform-style: preserve-3d;
}

.nav_menu li::before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--primary-color);
  transform: scaleX(0);
  transition: transform var(--transition-speed) ease;
}

.nav_menu li:hover {
  color: var(--primary-color);
  transform: translateY(-5px) rotateX(10deg);
}

.nav_menu li:hover::before {
  transform: scaleX(1);
}

.nav_login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}

.nav_login-cart button {
  width: 157px;
  height: 58px;
  border: none;
  border-radius: 75px;
  color: white;
  font-size: 20px;
  font-weight: 500;
  background: linear-gradient(45deg, var(--primary-color), #af60b8);
  cursor: pointer;
  transition: all var(--transition-speed) ease;
  position: relative;
  overflow: hidden;
}

.nav_login-cart button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.3) 0%, transparent 70%);
  transform: scale(0);
  transition: transform 0.5s ease;
}

.nav_login-cart button:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(255, 65, 65, 0.3);
}

.nav_login-cart button:hover::before {
  transform: scale(1);
}

.nav_login-cart img {
  width: 30px;
  height: 30px;
  transition: all var(--transition-speed) ease;
}

.nav_login-cart img:hover {
  transform: scale(1.2) rotateY(180deg);
  filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.2));
}



/* Glowing effect on hover */
@keyframes glow {
  0%, 100% { box-shadow: 0 0 5px rgba(255, 65, 65, 0.3); }
  50% { box-shadow: 0 0 20px rgba(255, 65, 65, 0.6); }
}


/* Responsive design */
@media (max-width: 1080px) {
  .Navbar {
    padding: 1px 10px;
  }
  .nav_menu {
    gap: 30px;
  }
  .nav_menu li {
    font-size: 18px;
  }
  .nav_login-cart button {
    width: 120px;
    height: 45px;
    font-size: 16px;
  }
}

