/* Container for the New Collections section */
.NewCollections {
    padding: 40px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 20px;
    text-align: center;
  }
  
  /* Title styling */
  .NewCollections h1 {
    font-size: 2.5em;
    color: #333;
    margin: 0;
    line-height: 1.2;
    transform: translateY(-20px);
    opacity: 0;
    animation: fadeInUp 1s forwards;
  }
  
  /* Horizontal rule styling */
  .NewCollections hr {
    border: 0;
    border-top: 2px solid #ddd;
    margin: 20px 0;
  }
  
  /* Container for collection items */
  .collections {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  /* Item container styling */
  .item {
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    overflow: hidden;
    margin: 10px;
    padding: 20px;
    width: 220px;
    text-align: center;
    cursor: pointer;
  }
  
  /* Hover effect for the item */
  .item:hover {
    transform: scale(1.05) rotateY(5deg); /* Slight 3D rotation */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  /* Image styling */
  .item img {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
    transition: transform 0.3s ease;
  }
  
  /* Hover effect for the image */
  .item:hover img {
    transform: scale(1.1);
  }
  
  /* Item name styling */
  .item p {
    font-size: 1.2em;
    margin: 10px 0;
    color: #333;
  }
  
  /* Container for prices */
  .item-prices {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* New price styling */
  .item-price-new {
    font-size: 1.5em;
    color: #e74c3c;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  /* Old price styling with strike-through */
  .item-price-old {
    font-size: 1.2em;
    color: #7f8c8d;
    text-decoration: line-through;
  }
  
  /* Animation for elements */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  