/* Container for the item */
.item {
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    overflow: hidden;
    margin: 10px;
    padding: 20px;
    width: 200px;
    text-align: center;
    cursor: pointer;
  }
  
  /* Hover effect for the item */
  .item:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }
  
  /* Image styling */
  .item img {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
    transition: transform 0.3s ease;
  }
  
  /* Hover effect for the image */
  .item:hover img {
    transform: scale(1.1);
  }
  
  /* Item name styling */
  .item p {
    font-size: 1.2em;
    margin: 10px 0;
    color: #333;
  }
  
  /* Container for prices */
  .item-prices {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* New price styling */
  .item-price-new {
    font-size: 1.5em;
    color: #e74c3c;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  /* Old price styling with strike-through */
  .item-price-old {
    font-size: 1.2em;
    color: #7f8c8d;
    text-decoration: line-through;
  }
  
  /* Animation for price changes */
  .item-price-new,
  .item-price-old {
    transition: color 0.3s ease;
  }
  
  /* Hover effect for price changes */
  .item:hover .item-price-new {
    color: #c0392b;
  }
  
  .item:hover .item-price-old {
    color: #95a5a6;
  }
  