/* ProductDisplay.css */

.ProductDisplay {
  display: flex;
  margin: 0 170px;
  gap: 50px;
  perspective: 1000px;
}

.productdisplay-left {
  display: flex;
  gap: 17px;
}

.productdisplay-img-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.productdisplay-img-list img {
  height: 163px;
  transition: transform 0.3s ease;
}

.productdisplay-img-list img:hover {
  transform: scale(1.05);
}

.productdisplay-main-img {
  width: 586px;
  height: 700px;
  transition: transform 0.5s ease;
}

.productdisplay-main-img:hover {
  transform: rotate3d(0, 1, 0, 5deg);
}

.productdisplay-right {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.productdisplay-right h1 {
  font-size: 40px;
  font-weight: 700;
  color: #3d3d3d;
  margin-bottom: 20px;
}

.productdisplay-right-star {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  gap: 5px;
  transform-style: preserve-3d;
  transition: transform 0.3s ease;
}

.productdisplay-right-star:hover {
  transform: scale(1.05) rotate3d(1, 0, 0, 10deg);
}

.productdisplay-right-star img {
  height: 20px;
}

.productdisplay-right-star p {
  font-size: 16px;
  color: #1c1c1c;
  margin-left: 10px;
}

.productdisplay-right-prices {
  display: flex;
  gap: 30px;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
}

.productdisplay-right-price-old {
  color: #818181;
  text-decoration: line-through;
}

.productdisplay-right-price-new {
  color: #ff4141;
}

.productdisplay-right-size h1 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.productdisplay-right-size {
  display: flex;
  gap: 20px;
}

.productdisplay-right-size div {
  padding: 18px 24px;
  background: #c16ec1;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.productdisplay-right-size div:hover {
  background: #ae75af;
  color: rgb(0, 0, 0);
  transform: translateY(-5px);
}

.productdisplay-right button {
  padding: 20px 40px;
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: rgb(0, 0, 0);
  background: #bc7fb8;
  margin-bottom: 20px;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.productdisplay-right button:hover {
  background: #ff2929;
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(255, 65, 65, 0.4);
}

.productdisplay-right-category {
  margin-bottom: 10px;
}

.productdisplay-right-category span {
  font-weight: 600;
}



