/* Container for the Footer section */
.Footer {
  margin: 1.3px;
    background: linear-gradient(135deg, #fafafa, #925792);
    padding: 0px;
    text-align: center;
    position: relative;
    overflow: hidden;
  
  }
  
  /* Logo and company name styling */
  .footer-logo {
    margin-bottom: 20px;
  }
  
  /* Logo image styling */
  .footer-logo img {
    width: 150px;
    height: auto;
    border-radius: 10px;
    transition: transform 0.3s ease;
  }
  
  .footer-logo img:hover {
    transform: scale(1.05);
  }
  
  /* Company name styling */
  .footer-logo p {
    font-size: 1.5em;
    margin-top: 10px;
    font-weight: bold;
    color: #030303;
  }
  
  /* Footer links styling */
  .footer-links {
    list-style: none;
    padding: 0;
    margin: 20px 0;
  }
  
  .footer-links li {
    display: inline;
    margin: 0 15px;
    font-size: 1.1em;
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .footer-links li:hover {
    color: #e74c3c;
    transform: scale(1.1);
  }
  
  /* Footer social icons styling */
  .footer-social-icons {
    margin: 20px 0;
  }
  
  .footer-icons-container {
    display: inline-block;
    margin: 0 10px;
  }
  
  .footer-icons-container img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .footer-icons-container img:hover {
    transform: scale(1.2);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  /* Footer copyright styling */
  .footer-copyright {
    margin-top: 20px;
  }
  
  .footer-copyright hr {
    border: 0;
    border-top: 1px solid #555;
    margin: 20px 0;
  }
  
  .footer-copyright p {
    font-size: 0.9em;
    color: #ccc;
  }
  
  /* Animation for elements */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .footer-logo img,
  .footer-links li,
  .footer-social-icons img,
  .footer-copyright p {
    animation: fadeInUp 1s forwards;
  }
  
  .footer-logo img {
    animation-delay: 0.2s;
  }
  
  .footer-links li {
    animation-delay: 0.4s;
  }
  
  .footer-social-icons img {
    animation-delay: 0.6s;
  }
  
  .footer-copyright p {
    animation-delay: 0.8s;
  }
  